import React, {useState} from "react";
import styled from "styled-components";
import Card from "./Card";

const CardsWrapper = styled.div`
    width: 60%;
    max-width: 576px;
    margin: 10px auto 0 auto;
    border: 4px solid #C97879;
    border-radius: 3px;
    background: linear-gradient(180deg, #E95D54 10.61%, rgba(237, 160, 89, 0.78) 89.8%);
    
    #image-name{
      text-align: center;
    }
    
    li{
       background: linear-gradient(180deg, #E95D54 10.61%, rgba(237, 160, 89, 0.78) 89.8%);
       border: 1px solid #DB988E;
       margin: 50px auto 100px auto;
       padding: 0px 32%;
       text-align: center;
    }
    @media(max-width: 992px) {
      li{
        padding: 0 30%;
    }
}

   @media(max-width: 667px) {
       li{
           padding: 0 20%;
       }
   }
   
   @media(max-width: 578px) {
       li{
           padding: 0 25%;
       }
       {
           width: 100%;
       }
    }
   
    @media(max-width: 337px) {
       li{
           padding: 5%;
       }
    }
   
    @media(max-width: 258px) {
       li{
           padding: 3%;
       }
    }
`;

const CardTable = (props) => {
   const [visibility, setVisibility] = useState(true);
   let data = visibility ? <ul>
      {props.cards.map((card, index) => {
             return <Card index={index} name={`${card.name}`} path={`${card.path}`}
                          key={index} id={`${card._id}`} removeCard={() => props.removeCard(`${card._id}`)}/>
          }
      )
      }
   </ul> : null;

   const showVisibility = () => {
      setVisibility(true);
   }
   const hideVisibility = () => {
      setVisibility(false);
   }

   return (
       <CardsWrapper>
          <center>
             <button onClick={() => showVisibility()}>Show Cards</button>
             <button onClick={() => hideVisibility()}>Hide Cards</button>
             <button onClick={() => props.removeAllCards()}>Delete All Cards</button>
          </center>
          {data}

       </CardsWrapper>
   );
}

export default CardTable;