import React from "react";
import styled from "styled-components";

const InputWrapper = styled.div`
    width: 60%;
    max-width: 768px;
    margin: 10px auto 0 auto;
    padding: 20px 5% 20px 5%;
    border: 4px solid #84CDA4;
    border-radius: 3px;
    //background-color: #B0E6BE;
    background: linear-gradient(110.77deg, #B2E282 49.69%, rgba(74, 225, 153, 0.78) 82.07%);
    .image{
       margin-top: 10px;
    }
    .description{
       margin-top: 10px;
    }
    
    #nameInput {
       margin-left: 10px;
       width: 75%;
    }
    #imageInput {
       margin-left: 9px;
       width: 75%;
    }
    #descriptionInput {
       margin-left: 9px;
       width: 65%;
    }    
    .buttons{
       margin-top: 10px;
       text-align: center;
    }
    
    .buttons button{
       padding: 2px 4px 2px 4px;
       border-radius: 3px;
    }
    
    #addButton{
       background-color: #1D861C;
   }
       
    @media(max-width: 576px) {
      width: 90%;
    };
                  
`;

const InputBar = (props) => {
   // console.log(props.value)
   const onChangeName = (e) => {
      props.onChangeName(e.target.value);
   }
   const onChangePath = (e) => {
      props.onChangePath(e.target.value);
   }
   const onChangeDescription = (e) => {
      props.onChangeDescription(e.target.value);
   }
   return (
       <InputWrapper className="submit">
          <form id="formInput">
             <div className="name">
                <label htmlFor="Name">Name:</label>
                <input type="text" id="nameInput" name="nameInput" placeholder="Add the name of the new image"
                       value={props.name} onChange={onChangeName} ref={props.nameRef}/><br/>
             </div>

             <div className="image">
                <label htmlFor="Image">Image:</label>
                <input type="text" id="imageInput" name="imageInput" placeholder="Enter the image path location"
                       value={props.path} onChange={onChangePath} ref={props.pathRef}/><br/>
             </div>

             <div className="description">
                <label htmlFor="description">Description:</label>
                <input type="text" id="descriptionInput" name="descriptionInput" placeholder="Enter the image description"
                       value={props.description} onChange={onChangeDescription} ref={props.descriptionRef}/><br/>
             </div>
          </form>
          <div className="buttons">
             <button id="addButton" onClick={() => props.onAdd()}>Add</button>
             <button id="clearButton" onClick={() => {
                props.onClear()
             }}>Clear
             </button>
          </div>
       </InputWrapper>
   );
}

export default InputBar;