import React, {useEffect, useRef, useState} from "react";
import styled from "styled-components";
import moment from "moment";

const Wrapper = styled.div`
    width: 40%;
    background: linear-gradient(51.04deg, #AC85DE 25.55%, rgba(127, 176, 213, 0.799188) 76.64%);
    text-align: center;
    margin: 70px auto 0 auto;
    padding: 20px;
    @media(max-width: 992px) {
    margin-top: 40px;
    width: 60%;
    };
    @media(max-width: 576px) {
    margin-top: 40px;
    width: 100%;
    };
    >div {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        > img{
         width: 250px;
         height: 250px;
         border-radius: 10px;
         border: 2px solid #80B1EC;
         max-width:90%;
       }
       .box{
           width: 375px;
           padding: 40px 0px;
           @media(max-width: 992px) {
              text-align: center;
           };
           .title{
               h1{
                   font-size: 24px;
                   color: #051221;
               }
           }
           .date{
              margin-top: 10px;
           }
           .content{
               padding: 20px 0px;
               p{
                   color: #EAEAEA};
                   padding-top: 10px;
                   line-height: 2;
               }
       }
       #editButton{
          background: linear-gradient(90deg, rgba(116, 230, 154, 0.72) 0%, rgba(139, 212, 212, 0.920305) 51.43%);
          padding: 5px;
          border-radius: 5px;
       }
       #editDescription{
          width: 90%;
       }
    }
`;

function CardPage(props) {
   const [card, setCard] = useState({});
   const [description, setDescription] = useState("");
   const descriptionRef = useRef(null);

   useEffect(() => {
      async function getTheCard() {
         setCard(await props.getCard(props.id));
      }
      getTheCard();
      setDescription(`${card.description}`);
   }, [props.id, card.description, props])


   const onChangeDescription = (e) => {
      setDescription(e.target.value);
   }
   const editContent = async () => {
      const newCard = {...card};
      newCard.description = description;
      await props.updateCard(props.id, newCard);
      setCard(await props.getCard(props.id));
   }
   return (
       <Wrapper>
          <div>
             <img src={`${card.path}`}
                  alt={`${card.name}`}
                  id={`${props._id}`}/>
             <div className="box">
                <div className="title">
                   <h1>{`${card.name}`}</h1>
                </div>
                <div className="date">
                   {moment(card.date).format('YYYY-MM-DD HH:mm:ss')}
                </div>
                <section className="content">
                   {`${card.description}`}
                </section>
                <textarea id="editDescription" name="editDescription" placeholder="Edit the description"
                          value={description} ref={descriptionRef} onChange={onChangeDescription}/>
                <button id="editButton" onClick={() => editContent()}>Edit Content</button>
             </div>
          </div>
       </Wrapper>
   );
}

export default CardPage;