import React from "react";
import {Link} from "react-router-dom";

const imageSize = {
   width: "208",
   height: "198"
}

const card = (props) => {
   return (
       <li key={props.index}>
          <Link to={`/cards/${props.id}`}>
             <div id={`${props.id}`}>{`${props.name}`}</div>
             <img src={`${props.path}`}
                  alt={`${props.name}`}
                  width={imageSize.width}
                  height={imageSize.height}
                  id={"image"}/>
          </Link>
          <button onClick={() => props.removeCard(`${props._id}`)}>Delete</button>
       </li>
   );
}

export default card;