import React, {useRef, useState} from "react";
import InputBar from "../compoments/InputBar";
import CardTable from "../compoments/CardTable";

function Home(props) {
   const defaultFormData = {
      name: "",
      path: "",
      description:"",
   }

   const onChangeName = (name) => {
      const newInput = {...input};
      newInput.name = name;
      setInput(newInput)
   }
   const onChangePath = (path) => {
      const newInput = {...input};
      newInput.path = path;
      setInput(newInput)
   }
   const onChangeDescription = (description) => {
      const newInput = {...input};
      newInput.description = description;
      setInput(newInput)
   }
   const submit = () => {
      props.addCard(input);
      alert("Add Successfully!");
      setInput(defaultFormData);
      nameRef.current.value = "";
      pathRef.current.value = "";
      descriptionRef.current.value = "";
   }

   const clear = () => {
      setInput(defaultFormData);
      nameRef.current.value = "";
      pathRef.current.value = "";
      descriptionRef.current.value = "";
   }

   const [input, setInput] = useState(defaultFormData);
   const nameRef = useRef(null);
   const pathRef = useRef(null);
   const descriptionRef = useRef(null);
   return (
       <div>
          <InputBar value={input} onAdd={submit} onClear={clear}
                    nameRef={nameRef} pathRef={pathRef} descriptionRef={descriptionRef}
                    onChangeName={(name) => onChangeName(name)}
                    onChangePath={(path) => onChangePath(path)}
                    onChangeDescription={(path) => onChangeDescription(path)}/>
          <CardTable cards={props.cards} removeCard={props.removeCard} removeAllCards={props.removeAllCards}/>
       </div>
   );
}

export default Home;