import React from "react";
import styled from "styled-components";
import NavBar from "./compoments/NavBar";
import {Route, Switch} from "react-router-dom";
import Home from "./view/Home";
import CardPage from "./view/CardPage";
import {useCards} from "./hooks/useCards";

const Wrapper = styled.div`
    //background: #FFFD97;
`;

function App() {
   const {cards, addCard, removeCard, removeAllCards, updateCard, getCard} = useCards();
   return (
       <Wrapper>
          <NavBar/>
          <Switch>
             <Route exact path="/">
               <Home cards={cards} removeCard={removeCard} removeAllCards={removeAllCards} addCard={addCard}/>
             </Route>
             <Route exact path="/cards/:id"
                    render={(props) => {
                       return <CardPage id={props.match.params.id} getCard={getCard}
                                        updateCard={updateCard}/>;
                    }}/>
          </Switch>
       </Wrapper>
   );
}

export default App;
