import {useEffect, useState} from "react";
const axios = require('axios');

const localhostURL = "http://localhost:7000";
const productionURL = "https://assignment-455-backend.herokuapp.com";

const useCards = () => { // Create a custom React Hook
   const [cards, setCards] = useState([]);

   useEffect( () => {
      async function initialSet() {
         const initCards = await getAllCards();
         setCards(initCards);
      }
      initialSet();
   }, [])

   const addCard = async (card) => {
      const date = new Date();
      const params = {
         name: card.name,
         path: card.path,
         date: date,
         description: card.description
      }
      // setCards([...cards, card]);
      await axios.post(`${productionURL}/cards/`, params);
      setCards(await getAllCards());
   }

   const updateCard = async (id, card) => {
      const params = {
         name: card.name,
         path: card.path,
         date: card.date,
         description: card.description
      }
      // setCards([...cards, card]);
      await axios.post(`${productionURL}/cards/${id}`, params);
      setCards(await getAllCards());
   }

   const removeCard = async (index) => {
      // setCards(cards.filter((element, position) => position !== index));
      await axios.delete(`${productionURL}/cards/${index}`);
      setCards(await getAllCards());
   }

   const removeAllCards = async () => {
      // setCards([])
      await axios.delete(`${productionURL}/cards/`);
      setCards(await getAllCards());
   }

   const getCard = async (id) => {
      const response = await axios.get(`${productionURL}/cards/${id}`);
      return response.data.card;
   }

   const getAllCards = async () => {
      const response = await axios.get(`${productionURL}/cards/`);
      return response.data.cardList;
   }

   return {cards, setCards, addCard, updateCard, removeCard, removeAllCards, getCard, getAllCards}
}
export {useCards};